.header-container {
    background-color: #00909e;
    height: 15vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header-text {
    font-size: 7vh;
    letter-spacing: 0.5vw;
    color: #f7f7f7;
    margin: auto auto;
}

.legend {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.legend-box {
    margin: auto 2vw;
    padding: 1vh 1vw;
    height: 3vh;
    width: 6vw;
    text-justify: auto;
    font-weight: bolder;
}

.box-container {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: auto;
}

.box-text-container {
    font-size: 2vh;
    margin: 2vh 2vw;
    border: 2px solid #00909e;
    display: flex;
}

.box-text {
    margin: auto auto;
}

.restart-btn {
    height: 5vh;
    width: fit-content;
    background-color: #649d66;
    color: #ffffff;
    font-size: larger;
}

.grid {
    border: 2px solid #00909e;
    height: fit-content;
    width: fit-content;
    grid-template-columns: repeat(auto, 1fr);
    overflow: auto;
}

.row { 
    display:flex;
}

.cell {
    border: 2px solid #000000;
    margin: 1vh 1vw;
    height: 4vh;
    width: 5vh;
}

.cell-btn {
    font-family: Helvetica, sans-serif;
    font-size: 2vh;
}

.footer-container {
    width: 100%;
    height: 7vh;
    background-color: #00909e;
    display: flex;
    flex-direction: column;
    position: absolute;
    bottom: 0vh;
}

.footer-text {
    color: #f7f7f7;
    margin: auto auto;
}

.winner {
    background-color: #649d66;
}

.loser {
    background-color: #c70039;
    color: #ffffff;
}

.cold {
    background-color: #0000CD;
    color: #ffffff;
}

.warm {
    background-color: #FFFF99;
    color: #000000;
}

.hot {
    background-color: #FF8C00 ;
    color: #000000;
}

